*{color:white;
    margin-left: 20px;
}
.row_posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;

}
.row_posters::-webkit-scrollbar{
    /* tell all the browser to display none but functioning yes */
    display: none;
}
.row_poster{
    object-fit:contain;
    width:100%;
    margin-right: 10px;
    max-height:100px;
    transition:transform 450ms ;
}
.row_poster:hover{
    transform: scale(1.08);
}
.row_posterLarger{
    max-height: 250px;
}
.row_posterLarger:hover{
    transform: scale(1.09);
}